import * as React from "react";
import { ActionMenuDescription } from "./action-menu-desc"
import ActionMenu from "./action-menu";

export default class ActionMenuBar extends React.Component<{ menuDescriptions: ActionMenuDescription[] }, {}> {

    render() {
        return <div className="pure-button-group button-xsmall action-menu" role="group" aria-label="...">
            {
                this.props.menuDescriptions.map((md) =>
                    <ActionMenu menuDescription={md} key={md.actionId} />
                )
            }
        </div>
    }
}

